.fade-enter,
.fade-appear{
  opacity: 0;
}

.fade-enter-active,
.fade-entering,
.fade-appear-active{
  opacity: 1;
  transition: opacity 400ms ease-in-out;
}

.fade-enter-done,
.fade-entered,
.fade-appear-done{
  opacity: 1 !important;
}

.fade-exit{
  opacity: 1;
}

.fade-exit-active{
  opacity: 0;
  transition: opacity 1s ease;
}

.fade-exit-done{
  opacity: 0;
}